import { Component, Input, Output, EventEmitter } from "@angular/core";

interface DropdownOption {
  id: string;
  name: string;
}

@Component({
  selector: "angular-weblineindia-dropdown",
  templateUrl: "./angular-weblineindia-dropdown.component.html",
  styleUrls: ["./angular-weblineindia-dropdown.component.css"],
})
export class AngularWeblineindiaDropdownComponent {
  @Input() placeHolder!: string;
  @Input() dropdownClass!: string | string[];
  @Input() dropdownListClass!: string | string[];
  @Input() disabled: boolean = false;
  @Input() options!: DropdownOption[];
  @Input() value!: string | number;

  selectedOption: DropdownOption | null = null;
  touched = false;

  @Output() change: EventEmitter<void> = new EventEmitter<void>(); 
  @Output() focus: EventEmitter<void> = new EventEmitter<void>();
  @Output() blur: EventEmitter<void> = new EventEmitter<void>();

  ngOnInit() {
    this.selectedOption =
      this.options.find((option) => option.id === this.value) || null;
  }

  onOptionSelect(event: any) {
    this.change.emit(event);
  }

  onFocus(event: any) {
    this.focus.emit(event);
  }

  onBlur(event: any) {
    this.blur.emit(event);
  }
}
